"use client";

import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import LoadingCenter from "@/components/loading";
import AuthenHook from "@/hooks/AuthenHook.mjs";
import axiosApi from "@/config/axios-api";
import { Input } from "@heroui/input";
import AppDataGrid from "@/components/table/AppDataGrid";
import { useTheme } from "next-themes";
import { useIdleTimer } from "react-idle-timer";
import Ripple from "@nuttawoot_donut/react-ripple";
import { motion } from "framer-motion";
import { Radio, RadioGroup } from "@heroui/radio";
import { pdf } from "@react-pdf/renderer";
import { addToast } from "@heroui/toast";
import ModalTextForm from "@/components/modal/ModalTextForm";
import { maskANPretty, maskCardIDPretty } from "@/utils/maskPretty";
import Forbidden from "@/components/Forbidden";
import HospitalHook from "@/hooks/HospitalHook.mjs";
import Developing from "@/components/Developing";
import { cryptoDecode } from "@/config/crypto.mjs";
import ClinicDynamicForm from "@/components/opd/ClinicDynamicForm";
import { Select, SelectItem } from "@heroui/select";
import { I18nProvider } from "@react-aria/i18n";
import { DatePicker } from "@heroui/date-picker";
import { parseDate } from "@internationalized/date";
import MRAFormOPD from "@/components/RenderForm/MRAFormOPD";

export default function Page() {
  const { profile, authLoading } = AuthenHook();
  const { setAutoSaveMessage } = HospitalHook();
  const { resolvedTheme } = useTheme();
  const [ token, setToken ] = useState(localStorage.getItem("token"));
  const [ mounted, setMounted ] = useState(false);
  const [ vn, setVn ] = useState("");
  const [ hcode, setHcode ] = useState({ hcode: "", hcode_name: "" });

  const [ clinicDetail, setClinicDetail ] = useState(null);
  const [ clinicDetailSelect, setClinicDetailSelect ] = useState(null);
  const [ clinicDetailValue, setClinicDetailValue ] = useState(null);
  const [ clinicDetailInput, setClinicDetailInput ] = useState(null);
  const [ fieldClinic, setFieldClinic ] = useState({}); // เก็บฟิลด์ข้อมูลที่จะใช้บันทึกและ render ui

  const [ rowData, setRowData ] = useState([]);
  const [ reviewStatus, setReviewStatus ] = useState(null);
  const [ pdfBase64, setPdfBase64 ] = useState(""); 

  const [ loadingDelete, setLoadingDelete ] = useState(false);
  const [ loadingDataTable, setLoadingDataTable ] = useState(false);
  const [ loadingSubmit, setLoadingSubmit ] = useState(false);

  const [ formData, setFormData ] = useState({
    content: [],
    review_status_id: null,
    review_status_result: null,
    review_status_comment: "",
  });

  const [ patientVn, setPatientVn ] = useState(null);

  const [ openModelDelete, setOpenModelDelete ] = useState(false);

  const [ generatePdf, setGeneratePdf ] = useState(false);

  const [pdfReady, setPdfReady] = useState(false);
  const [pdfData, setPdfData] = useState(null);
  const pdfRef = useRef();

  let SaveAutoTime = 2000 // 2 วินาที
 
  const inputRef = useRef(null); // เก็บตัวเก็บข้อมูลของ input

  const ripple = new Ripple();

  useEffect(() => setMounted(true), []);

  useEffect(() => {
    try { setToken(localStorage.getItem("token")); } catch {}
  }, []);

  if (!token) return <Forbidden />;

  if (token === null) return <LoadingCenter />

  // if(profile?.email !== "iinter731@gmail.com" && profile?.email !== "chanathipbadboy@gmail.com") {
  //   return (
  //     <Developing 
  //       title="แบบประเมินคุณภาพการบันทึกเวชระเบียนผู้ป่วยนอก (OPD)"
  //       message="ระบบกำลังอยู่ในระหว่างการพัฒนา"
  //       subMessage="ขออภัยในความไม่สะดวกครับ/ค่ะ"
  //       progress={70}
  //       est="ETA : คาดว่าจะเสร็จภายในสัปดาห์นี้"
  //       actions={[
  //         { label: "กลับไปหน้า Dashboard", href: "/", primary: true },
  //         { label: "รีเฟรช", onClick: () => location.reload() },
  //       ]}
  //     />
  //   )
  // }

  const onGridReady = useCallback((event) => {
    event.api.sizeColumnsToFit();
  }, []);

  const fetchHcode = async () => {
    const token = localStorage.getItem("token");
    try {
      const res = await axiosApi.get("/setting/hcode", {
        headers: { Authorization: `Bearer ${token}` },
      })

      if(res.status === 200){
        setHcode(res.data.data[0]);
      }
    } catch (err) {
      console.log(err)
    }
  }

  const fetchClinicDetails = async () => {
    const token = localStorage.getItem("token");
    try {
      const res = await axiosApi.get("/setting/clinicalDetails", {
        headers: { Authorization: `Bearer ${token}` },
      })

      if(res.status === 200){
        setClinicDetail(res.data.data);
      }
    } catch (err) {
      console.log(err)
    }
  }

  const fetchOnePatient = async (vnKey) => {
    setPdfReady(false);
    try {
      const res = await axiosApi.get(`/mraOpd/fetchPatient/${vnKey}`, {
        headers: { Authorization: `Bearer ${token}` }, // ใช้ token จาก hook
      });
      
      if (res.status === 200) {
        fetchClinicDetails();
        let deHash = await cryptoDecode(res.data.data);
        setPatientVn(deHash); // เก็บข้อมูลคนไข้จาก API นี้
        addToast({ title: "สําเร็จ", description: res.data.message, timeout: 8000, color: "success", shouldShowTimeoutProgress: true });
      }

    } catch (err) {
      let passClinic = true;
      console.log(err);
      if (err?.response?.status === 409) {
        await generateForm(vnKey, passClinic);
        return;
      } else {
        addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
      }
    }
  };

  const fetchCheckClinicStatus = async (id) => {
    try {
      const res = await axiosApi.get(`/mraOpd/fetchClinicalDetail/${id}`, {
        headers: { 
          Authorization: `Bearer ${token}` 
        },
      })

      if(res.status === 200){
        setFieldClinic(res.data.data);
      }
    } catch (err) {
      console.log(err);
    }
  }

  const handleIdle = () => { // ฟังก์ชันสําหรับการหยุดพิมพ์
    if (vn?.trim()) {
      fetchOnePatient(vn);
    }
  };

  useIdleTimer({
    timeout: 1000, // 1000ms = หยุดพิมพ์
    onIdle: handleIdle, // ใช้ฟังก์ชัน handleIdle
    events: ["keyup"], // ใช้ keyup เท่านั้น
    element: inputRef.current, // ผูกกับ input
    immediateEvents: [], // ไม่ต้องรัน event อื่นทันที
  });

const generateForm = async (arg1, arg2, arg3) => {

  console.log("generate")

  let e = null;
  let vnParam = vn;
  let payload = {};
  let passClinic = false;
  let clinicalInput = clinicDetailInput;

  // โหมด 1: มาจาก onSubmit ของฟอร์ม
  if (arg1 && typeof arg1.preventDefault === "function") {
    e = arg1;
    e.preventDefault?.();
    clinicalInput = arg2 ?? clinicDetailInput;
    passClinic = false;
  } else {
    // โหมด 2: เรียกด้วย vnKey, passClinic
    vnParam = String(arg1 ?? vn).trim();
    passClinic = Boolean(arg2);
  }

  if (!vnParam) return;

  setLoadingDataTable(true);

  if (passClinic) {
    // ใช้แค่ VN เพื่อให้ backend สร้างฟอร์มจาก default ของเคส
    payload = { patient_vn: vnParam };
  } else {
    // ต้องมี clinic detail ให้ครบ
    payload = {
      patient_vn: vnParam,
      clinical_detail_id: clinicDetailSelect?.clinical_detail_id,
    };
  }

  try {
    const res = await axiosApi.post("/mraOpd", payload, {
      headers: { Authorization: `Bearer ${token}` },
    });

    if (res.status === 200) {
      addToast({
        title: "สําเร็จ",
        description: "สร้างแบบฟอร์มสําเร็จ",
        timeout: 8000,
        color: "success",
        shouldShowTimeoutProgress: true,
      });

      const dec = await cryptoDecode(res.data.data);

      setRowData(dec[0].form_opd_content_of_medical_record_results);
      fetchReviewStatus();

      // ตั้งค่า clinic detail ครั้งแรกที่ได้กลับมา
      if (!clinicDetailSelect) {
        const picked = dec[0].form_opd_clinical_detail_results?.[0]?.clinical_details;
        setClinicDetailSelect(picked || null);
        setClinicDetailValue(dec[0].form_opd_clinical_detail_results?.[0] || null);
        fetchClinicDetails();
        if (picked?.clinical_detail_id) {
          fetchCheckClinicStatus(picked.clinical_detail_id);
        }
      }

      // ผู้ป่วย
      const p = (Array.isArray(dec) ? dec[0]?.patients : dec?.patients) || {};
      setPatientVn({
        vn: p.patient_vn || "",
        hn: p.patient_hn || "",
        cid: p.patient_cid || "",
        fullname: p.patient_fullname || "",
        regdate: p.patient_date_admitted || "",
        dcgdate: p.patient_date_discharged || "",
        ward_name: p.patient_ward || "",
      });

      // map content -> payload สำหรับบันทึกต่อ
      setFormData((output) => ({
        ...output,
        content: dec[0].form_opd_content_of_medical_record_results.map((item) => {
          const {
            created_at,
            updated_at,
            created_by,
            updated_by,
            total_score,
            score_obtained,
            content_of_medical_records,
            ...rest
          } = item;

          const content_of_medical_record_id =
            content_of_medical_records?.content_of_medical_record_id;

          const converted = { content_of_medical_record_id };

          for (const key in rest) {
            const value = rest[key];
            if (key === "comment") {
              converted[key] = value ?? null;
            } else if (key === "na" || key === "missing" || key === "no") {
              converted[key] = !!value;
            } else {
              converted[key] =
                typeof value === "boolean"
                  ? value ? 1 : 0
                  : value == null
                  ? 0
                  : value;
            }
          }

          return converted;
        }),
      }));
    }
  } catch (err) {
    console.log(err);
    if(err.response.status === 409) { // สำหรับข้อผิดพลาด 409
        hdlPreviewSubmit(); // ส่งข้อมูลไปยังฟังก์ชัน hdlPreviewSubmit
      } else {
        addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
      }
  } finally {
    setLoadingDataTable(false);
  }
};

  useEffect(() => {
    fetchHcode();
    fetchClinicDetails();
    inputRef.current.focus();
    
  }, [authLoading, token, profile]);

  const fetchReviewStatus = async () => {
    try {
      const res = await axiosApi.get("/mraOpd/rvst", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })

      if(res.status === 200){
        setReviewStatus(res.data.data);
      }
    } catch (err) {
      console.log(err)
    }
  }

  const hdlPreviewSubmit = async () => {
    setGeneratePdf(true);
    try {
      // 1. ดึงข้อมูล API (หรือใช้ข้อมูลที่มีใน state)
      const rs = await axiosApi.get(`/mraOpd/${vn}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }) // หรือรับจาก props

      const data = await cryptoDecode(rs.data.data);

      if(rs.status === 200){
        setPdfReady(true);
        setPdfData(data[0]);
        const p = (Array.isArray(data) ? data[0]?.patients : data?.patients) || {};
        setPatientVn({
          vn: p.patient_vn || "",
          hn: p.patient_hn || "",
          cid: p.patient_cid || "",
          fullname: p.patient_fullname || "",
          regdate: p.patient_date_admitted || "",
          dcgdate: p.patient_date_discharged || "",
          ward_name: p.patient_ward || "",
        });

        const picked = data[0].form_opd_clinical_detail_results?.[0]?.clinical_details;
        setClinicDetailSelect(picked || null);
        setClinicDetailValue(data[0].form_opd_clinical_detail_results?.[0] || null);
        fetchClinicDetails();
        if (picked?.clinical_detail_id) {
          fetchCheckClinicStatus(picked.clinical_detail_id);
        }
        setTimeout(() => {
          if (pdfRef.current) {
            pdfRef.current.focus();
          }
        }, 500);
      }
    } catch (err) {

      // -----------------error 409 คือ ได้รับไฟล์ PDF-----------------------
      if(err.response.status === 409) {
        setPdfReady(true); //--- ให้แสดงไฟล์ PDF
        setPdfBase64(err.response?.data.data?.pdf_opd_file); //--- ให้ข้อมูล PDF
        const p = err.response.data.data?.form_opds?.patients;
        setPatientVn({
          vn: p.patient_vn || "",
          hn: p.patient_hn || "",
          cid: p.patient_cid || "",
          fullname: p.patient_fullname || "",
          regdate: p.patient_date_admitted || "",
          dcgdate: p.patient_date_discharged || "",
          ward_name: p.patient_ward || "",
        });

        let data = err.response.data.data.form_opds;

        const picked = data.form_opd_clinical_detail_results?.[0]?.clinical_details;
        setClinicDetailSelect(picked || null);
        setClinicDetailValue(data.form_opd_clinical_detail_results?.[0] || null);
        
        fetchClinicDetails();
        if (picked?.clinical_detail_id) {
          fetchCheckClinicStatus(picked.clinical_detail_id);
        }

      } else {
        addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
      }
    } finally {
      setGeneratePdf(false);
    }
  }

  const isRowLockedExceptSelf = (data, columnKey) => {
    if (columnKey === 'na') return data?.missing
    if (columnKey === 'missing') return data?.na;
    // คอลัมน์อื่นๆ
    return data?.na || data?.missing;
  };

  const columnDefs = useMemo(() => ([
    { 
      field: "content_of_medical_record_name",
      pinned: 'left',
      headerName: "หัวข้อเวชระเบียน", 
      valueGetter: (params) => {
        const name = params.data?.content_of_medical_records?.content_of_medical_record_name || "";
        return `${name}`
      },
      minWidth: 180, 
    },
    { 
      field: "follow_up_date", 
      headerName: "วันที่ติดตาม",
      cellEditor: "agTextCellEditor",
      minWidth: 180,
      pinned: 'left',
      cellRenderer: (params) => {
        const enable = params.data.content_of_medical_records?.follow_up_date_type !== false;

        if (!enable) return null;

        // ถ้ามีค่าเดิม เป็น yyyy-mm-dd ให้โชว์ใน DatePicker ด้วย
        const cur = params.data.follow_up_date?.slice(0, 10);
        const defaultVal = typeof cur === "string" && cur.length === 10 ? parseDate(cur) : undefined;

        return (
          <I18nProvider locale="th-TH">
            <DatePicker
              aria-label="วันที่ติดตาม"
              variant="underlined"
              size="md"
              showMonthAndYearPickers
              defaultValue={defaultVal}
              onChange={(d) => {
                const s = d
                  ? `${d.year}-${String(d.month).padStart(2, "0")}-${String(
                      d.day
                    ).padStart(2, "0")}`
                  : ""; // ไม่เลือก = ค่าว่าง
                params.node.setDataValue("follow_up_date", s);
              }}
            />
          </I18nProvider>
        );
      },
      stopEditingWhenCellsLoseFocus: true,
      editable: (params) => {
        return !isRowLockedExceptSelf(params.data, 'follow_up_date') && params.data.content_of_medical_records?.follow_up_date_type !== false;
      },
    },
    { field: "na", 
      editable: (params) => {
        return !isRowLockedExceptSelf(params.data, 'na') && params.data.content_of_medical_records?.na_type !== false;
      },
      headerName: "NA", 
      minWidth: 60,
      cellRenderer: "agCheckboxCellRenderer",
      cellEditor: "agCheckboxCellEditor",
      cellStyle: (params) => {
        if (isRowLockedExceptSelf(params.data, 'na') || params.data.content_of_medical_records?.na_type === false) {
          return {
            backgroundColor: mounted && resolvedTheme === "dark" ? '#303030' : '#D7D7D7',
            cursor: "not-allowed",
            userSelect: "none",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          };
        }
        return {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          cursor: "pointer",
        };
      },
    },
    { 
      field: "missing",
      headerName: "Missing",
      minWidth: 80,
      cellRenderer: "agCheckboxCellRenderer",
      cellEditor: "agCheckboxCellEditor",
      editable: (params) => {
        return !isRowLockedExceptSelf(params.data, 'missing') && params.data.content_of_medical_records?.missing_type !== false;
      },
      cellStyle: (params) => {
        if (isRowLockedExceptSelf(params.data, 'missing') || params.data.content_of_medical_records?.missing_type === false) {
          return {
            backgroundColor: mounted && resolvedTheme === "dark" ? '#303030' : '#D7D7D7',
            cursor: "not-allowed",
            userSelect: "none",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          };
        }
        return {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        };
      },
    },
    ...Array.from({ length: 7 }, (_, i) => {
      const field = `criterion_number_${i + 1}`;          // ค่าจริง 0/1 เก็บคอลัมน์นี้
      const flagKey = `${field}_type`;                    // แฟลกสำหรับล็อก/ปิดการแก้ไข (boolean/0/1)

      return {
        field,
        headerName: `เกณฑ์ ${i + 1}`,
        minWidth: 80,
        cellRenderer: "agCheckboxCellRenderer",
        cellEditor: "agCheckboxCellEditor",
        cellDataType: "boolean", 
        suppressClickEdit: true,
        stopEditingWhenCellsLoseFocus: true,
        editable: (params) => {
          const locked = isRowLockedExceptSelf?.(params.data, field);
          const disabledByFlag = params.data?.content_of_medical_records?.[flagKey] === false;
          return !locked && !disabledByFlag;
        },

        onCellClicked: (params) => {
          const locked = isRowLockedExceptSelf?.(params.data, field);
          const disabledByFlag = params.data?.content_of_medical_records?.[flagKey] === false;
          if (locked || disabledByFlag) return;     // เคารพเงื่อนไขล็อก

          const next = !Boolean(params.value);      // params.value คือ boolean จาก valueGetter
          // ใช้ setDataValue เพื่อให้ valueSetter ทำงานและยิง cellValueChanged ให้ครบ
          params.node.setDataValue(field, next);
        },

        cellStyle: (params) => {
          const locked = isRowLockedExceptSelf?.(params.data, field);
          const disabledByFlag = params.data?.content_of_medical_records?.[flagKey] === false;

          if (locked || disabledByFlag) {
            return {
              backgroundColor: mounted && resolvedTheme === "dark" ? "#303030" : "#D7D7D7",
              cursor: "not-allowed",
              userSelect: "none",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            };
          }
          return {
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            cursor: "pointer",
          };
        },

        // 0/1 -> true/false ให้ editor ใช้
        valueGetter: (params) => {
          const v = params.data?.[field];
          return v === 1; // ถ้าเป็น 1 คือ true ที่เหลือให้เป็น false ไป
        },

        // true/false -> 0/1 กลับเข้า data
        valueSetter: (params) => {
          params.data[field] = params.newValue ? 1 : 0;
          return true; // บอกกริดว่ามีการเปลี่ยนค่าแล้ว
        },
      };
    }),
    {
      field: "point_awarded",
      headerName: "เพิ่มคะแนน",
      minWidth: 90,
      cellRenderer: "agCheckboxCellRenderer",
      cellEditor: "agCheckboxCellEditor",
      cellDataType: "boolean",
      suppressClickEdit: true,
      stopEditingWhenCellsLoseFocus: true,
      valueGetter: (p) => p.data?.point_awarded === 1 || p.data?.point_awarded === true,
      valueSetter: (p) => { p.data.point_awarded = p.newValue ? 1 : 0; return true; },
      editable: (params) => {
        return !isRowLockedExceptSelf(params.data, 'point_awarded') && params.data.content_of_medical_records?.points_awarded_type !== false;
      },
      onCellClicked: (p) => {
        if (isRowLockedExceptSelf(p.data, "point_awarded") || 
            p.data.content_of_medical_records?.points_awarded_type === false) return;
        p.node.setDataValue("point_awarded", !p.value);
      },
      cellStyle: (params) => {
        if (isRowLockedExceptSelf(params.data, 'point_awarded') || params.data.content_of_medical_records?.points_awarded_type === false) {
          return {
            backgroundColor: mounted && resolvedTheme === "dark" ? '#303030' : '#D7D7D7',
            cursor: "not-allowed",
            userSelect: "none",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          };
        }
        return {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          cursor: "pointer",
        };
      },
    },
    {
      field: "point_deducted",
      headerName: "หักคะแนน",
      minWidth: 90,
      cellRenderer: "agCheckboxCellRenderer",
      cellEditor: "agCheckboxCellEditor",
      cellDataType: "boolean",
      suppressClickEdit: true,
      stopEditingWhenCellsLoseFocus: true,
      valueGetter: (p) => p.data?.point_deducted === 1 || p.data?.point_deducted === true,
      valueSetter: (p) => { p.data.point_deducted = p.newValue ? 1 : 0; return true; },
      editable: (params) => {
        return !isRowLockedExceptSelf(params.data, 'point_deducted') && params.data.content_of_medical_records?.points_deducted_type !== false;
      },
      onCellClicked: (p) => {
        if (isRowLockedExceptSelf(p.data, "point_deducted") || 
            p.data.content_of_medical_records?.points_deducted_type === false) return;
        p.node.setDataValue("point_deducted", !p.value);
      },
      cellStyle: (params) => {
        if (isRowLockedExceptSelf(params.data, 'point_deducted') || params.data.content_of_medical_records?.points_deducted_type === false) {
          return {
            backgroundColor: mounted && resolvedTheme === "dark" ? '#303030' : '#D7D7D7',
            cursor: "not-allowed",
            userSelect: "none",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          };
        }
        return {
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          cursor: "pointer",
        };
      },
    },
    { field: "comment", 
      headerName: "หมายเหตุ",
      cellEditor: "agTextCellEditor",
      editable: true, 
      minWidth: 220,
      stopEditingWhenCellsLoseFocus: true,
      onCellClicked: (p) => p.api.startEditingCell({
        rowIndex: p.rowIndex,
        colKey: "comment",
      }),
    },
  ]));

  const hdlCellValueChanged = (event) => {
    event.api.refreshCells({ rowNodes: [event.node], force: true });

    const updatedData = []; // สร้างตัวแปรสําหรับเก็บข้อมูล

    event.api.forEachNode((node) => { // วนลูปใน AGGrid
      updatedData.push({ ...node.data }); // เพิ่มข้อมูลใน node.data ไปยัง updatedData
    }); // วนลูปใน AGGrid

    setFormData((output) => ({
      ...output,
      content: updatedData.map((item) => {
        const {
          created_at,
          updated_at,
          created_by,
          updated_by,
          total_score,
          content_of_medical_records,
          score_obtained,
          ...rest
        } = item;

        const content_of_medical_record_id = content_of_medical_records?.content_of_medical_record_id;

        const converted = {
          content_of_medical_record_id,
        };

        for (const key in rest) {
          const value = rest[key];
          if (key === "comment") {
            converted[key] = value ?? null;
          } else if (key === "follow_up_date") {
            // ต้องเป็น "" หรือ "yyyy-mm-dd"
            converted[key] = value ? String(value) : ""; 
          } else if (key === "na" || key === "missing" || key === "no") {
            // na, missing, no คง true/false ตามเดิม
            converted[key] = !!value;
          } else {
            converted[key] =
                typeof value === "boolean" ? (value ? 1 : 0)
                : value == null ? 0  // สำหรับ null หรือ undefined
              : value;
          }
        }

        return converted;
      }),
    }));
  }

  const onSubmit = async () => {
    setLoadingSubmit(true);
    let token = localStorage.getItem("token");
    try {
      const res = await axiosApi.put(`/mraOpd/${vn}`, formData, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })

      if(res.status === 200){
        fetchOnePatient(vn);
      }
    } catch (err) {
      console.log(err)
      addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    } finally {
      setLoadingSubmit(false);
    }
  }

  const onAutoSave = async () => {
    setAutoSaveMessage({ type: "info", message: "กําลังบันทึกอัตโนมัติ..." });
    let token = localStorage.getItem("token");
    try {
      const res = await axiosApi.put(`/mraOpd/${vn}`, formData, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })

      if(res.status === 200){
        // fetchOnePatient(vn);
        setAutoSaveMessage({ type: "success", message: "บันทึกอัตโนมัติสําเร็จ"});
      }
    } catch (err) {
      console.log(err)
      setAutoSaveMessage({ type: "danger", message: "บันทึกอัตโนมัติไม่สําเร็จ"});
    } finally {
      setTimeout(() => {
        setAutoSaveMessage("");
      }, 5000);
    } 
  }

  // ฟังก์ชันสําหรับการหยุดทุกการกระทําแล้วจะนับเวลา 5 วินาที แล้วบันทึก (Auto Save)
  useIdleTimer({
    timeout: SaveAutoTime, // 5000ms = 5 วินาที
    onIdle: ( patientVn && !pdfReady && formData.review_status_id === null && rowData.length > 0) ? onAutoSave : null, // ใช้ฟังก์ชัน handleIdle
    events: ["keydown", "keyup", "input", "change"], // เหตุการณ์ที่ต้องการตรวจจับ
    element: typeof document !== "undefined" ? document : null, // ผูกกับ document ทั้งหมด
    immediateEvents: [], // ไม่ต้องรัน event อื่นทันที
    startOnMount: false, // ไม่เริ่มจับเวลาเมื่อ component ถูก mount
    debounce: 500, // หน่วงเวลา 500ms ก่อนเริ่มจับเวลาใหม่หลังจากมีเหตุการณ์
  })

  const hdlChangeReviewStatus = (value) => {
    const rvs = reviewStatus?.find(e => e.review_status_id === value)
    setFormData(prev => ({
      ...prev, 
      review_status_id: rvs.review_status_id,
      review_status_result: true,
      review_status_comment: rvs.review_status_comment
    }))
  }

  const hdlClear = () => {
    setFormData(prev => ({
      ...prev, 
      content: [],
      review_status_id: null,
      review_status_result: null,
      review_status_comment: ""
    }))
  }

  const generateBase64 = async (pdfData) => {
    const blob = await pdf(<MRAFormOPD {...pdfData} />).toBlob();
    const base64 = await blobToBase64(blob);
    return base64;
  };

  useEffect(() => {
    // ต้องพร้อมแสดง PDF จริงๆ และมี id ให้ผูกไฟล์
    if (!pdfData || !pdfData.form_opd_id) return;

    let aborted = false;
    (async () => {
      const base64 = await generateBase64(pdfData);
      if (aborted) return;
      setPdfBase64(base64);
      await sendPpfFile(base64, pdfData.form_opd_id);
    })();
    return () => { aborted = true; };
  }, [pdfReady, pdfData]);

  const sendPpfFile = async (b64, id) => {

    const body = {
      form_opd_id: id,
      binary: b64,
      total_score: pdfData.totalScoreSum,
      percentage: pdfData.formattedResultSum,
      score_obtained: pdfData.scoreObtained
    }

    try {
      const res = await axiosApi.post(`/mraOpd/insertPdf`, body, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })

      if (res.status === 200) {
        addToast({ title: "สําเร็จ", description: res.data.message, timeout: 8000, color: "success", shouldShowTimeoutProgress: true });
      }
    } catch (err) {
      console.log(err)
      addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    }
  }

  const blobToBase64 = (blob) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => resolve(reader.result.split(',')[1]); // ได้เป็น pure base64 string
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
  };

  const hdlDelete = async ( output ) => {
    setLoadingDelete(true);
    if (output.patient_vn !== vn) {
      return addToast({ title: "เกิดข้อผิดพลาด", description: "หมายเลข VN ไม่ถูกต้อง", timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    }

    const body = { password: output.password };

    try {
      const res = await axiosApi.delete(`/mraOpd/${output.patient_vn}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }, data: body })

      if(res.status === 200){
        addToast({ title: "สําเร็จ", description: res.data.message, timeout: 8000, color: "success", shouldShowTimeoutProgress: true });
        hdlClear();
        setVn("");
        setPatientVn(null);
        setPdfData(null);
        setPdfBase64("");
        setPdfReady(false);
        setRowData([]);
        setOpenModelDelete(false);
        setClinicDetailSelect(null);
        setClinicDetail([]);
        setClinicDetailSelect(null);
        setClinicDetailValue(null);
        setClinicDetailInput(null);
      }

    } catch (err) {
      console.log(err)
      addToast({ title: "เกิดข้อผิดพลาด", description: err.response.data.message, timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
    } finally {
      setLoadingDelete(false);
    }
  }

  const field = [
    {
      type: "number",
      name: "patient_vn",
      label: "กรอกหมายเลข VN",
      placeholder: "XXXXXXXXX",
      required: true,
      radius: "sm",
    },
    {
      type: "password",
      name: "password",
      label: "กรอกรหัสผ่าน",
      placeholder: "**********",
      required: true,
      radius: "sm",
    },
  ]

  const hdlSelectClinicalDetail = (id) => {
    fetchCheckClinicStatus(id);
    const r = clinicDetail?.find(e => e.clinical_detail_id === id)
    setClinicDetailSelect(r);
  }

  const hdlClearInputVN = () => {
    setPatientVn(null);
    setRowData([]);
    setPdfReady(false);
    setPdfData(null);
    hdlClear();
    setPdfBase64("");
    setClinicDetail([]);
    setClinicDetailSelect(null);
    setClinicDetailValue(null);
    setClinicDetailInput(null);
  }

  return (
    <>
    {(loadingDelete || loadingSubmit) && (
        <div className="fixed inset-0 z-50 flex items-center justify-center dark:bg-black/80 bg-white/40 backdrop-blur-sm">
          <LoadingCenter title={loadingDelete ? "กําลังลบข้อมูล..." : "กําลังบันทึกข้อมูล..."} />
        </div>
      )}
      <ModalTextForm isOpen={openModelDelete} onClose={() => setOpenModelDelete(false)} delModal={true} title={`คุณต้องการลบข้อมูล ${maskANPretty(vn, { keepStart: 2, keepEnd: 2 })}`} onSubmit={hdlDelete} fields={field} />
      <div className="select-none rounded-2xl border border-zinc-300 dark:border-[#505050] mb-4 bg-white dark:bg-[#181818] shadow-lg">
        <div className="flex justify-between border-b border-zinc-300 dark:border-[#505050] p-4">
          <div className="flex gap-1 flex-col">
            <p className="font-medium">แบบตรวจประเมินคุณภาพการบันทึกเวชระเบียนผู้ป่วยนอก Medical Record Audit Form (OPD)</p>
            <p className="text-xs font-medium text-zinc-600 dark:text-zinc-500"><span className="font-semibold mr-1">เงื่อนไขการบันทึกเวชระเบียน</span>การบันทึกคะแนน (1) กรณีที่ผ่านเกณฑ์ในแต่ละข้อ ให้ติ๊กถูก (2) กรณีที่ไม่ผ่านเกณฑ์ในแต่ละข้อ ไม่ต้องติ๊ก (3) กรณีไม่จำเป็นต้องมีบันทึก/ไม่มีข้อมูล ในเกณฑ์ข้อที่ระบุให้มี NA ได้ ให้ NA</p>
          </div>
          {patientVn?.ward_name && (
            <span className="shrink-0 h-fit rounded-full border border-zinc-300 dark:border-zinc-700 px-3 py-1 text-xs text-zinc-700 dark:text-zinc-200 bg-zinc-50 dark:bg-zinc-900/60">
              {patientVn.ward_name}
            </span>
          )}
        </div>
        <div className="p-4 flex flex-col gap-2">
          <div className="flex gap-2">
            <Input variant="bordered" radius="md" label="รหัสสถานพยาบาล" size="md" placeholder="XXXXX" value={hcode ? hcode.hcode : ""} labelPlacement="outside-top" isReadOnly />
            <Input variant="bordered" radius="md" label="ชื่อสถานพยาบาล" size="md" placeholder="โรงพยาบาล....." value={hcode ? hcode.hcode_name : ""} labelPlacement="outside-top" isReadOnly />
          </div>
          <div className="flex gap-2">
            <Input variant="bordered" radius="md" label="บัตรประชาชน" size="md" placeholder="บัตรประชาชน" value={patientVn ? maskCardIDPretty(patientVn.cid, { keepStart: 9, keepEnd: 0, maskChar: "*" }) : ""} labelPlacement="outside-top" isDisabled />
            <Input variant="bordered" radius="md" label="ชื่อ-สกุล" size="md" placeholder="ชื่อ-สกุล" value={patientVn ? patientVn.fullname : ""} labelPlacement="outside-top" isDisabled />
            <Input variant="bordered" radius="md" label="HN" size="md" placeholder="HN" value={patientVn ? patientVn.hn : ""} labelPlacement="outside-top" isDisabled />
          </div>
          <hr className="border-zinc-300 dark:border-zinc-700 mt-4 mb-2" />
          <motion.form onSubmit={(e) => generateForm(e, clinicDetailInput)} className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-4 xl:grid-cols-5 gap-2">
            <Input 
              ref={inputRef}
              value={vn}
              onChange={(e) => {
                setVn(e.target.value);
                hdlClearInputVN();
              }}
              variant="bordered"
              radius="md"
              label="VN"
              size="md"
              placeholder="VN"
              labelPlacement="outside-top"
              maxLength={12}
              onClear={ () => {
                setVn("");
                hdlClearInputVN();
              }}
              isRequired
            />
            <div className="pt-1">
               {clinicDetail && (
                  <Select
                    label="Case"
                    placeholder="Case"
                    labelPlacement="outside"
                    variant="bordered"
                    classNames={{ label: "text-black" }}
                    selectedKeys={clinicDetailSelect?.clinical_detail_id ? new Set([String(clinicDetailSelect.clinical_detail_id)]) : new Set()}
                    onSelectionChange={(keys) => {
                      const id = [...keys][0] ?? "";
                      hdlSelectClinicalDetail(id);            // id คือ clinical_detail_id ที่เลือก
                    }}
                    isDisabled={!patientVn || rowData.length > 0 || pdfReady}
                    isRequired
                  >
                    {clinicDetail?.map((item) => (
                      <SelectItem autoFocus key={String(item.clinical_detail_id)}>
                        {item.clinical_detail_name}
                      </SelectItem>
                    ))}
                  </Select>
                )}
            </div>
            {/* <div className="col-span-3"> */}
            {/* { clinicDetailSelect && (
              <ClinicDynamicForm data={fieldClinic} value={clinicDetailValue} onChange={(data) => setClinicDetailInput(data)} length={rowData.length} />
            )} */}
            {/* </div> */}
            <div className="flex flex-col items-center w-full h-full">
              <div className="flex items-center justify-between w-full">
                <p className="text-sm mb-2">ตัวเลือก</p>
              </div>
              <div className="w-full items-center gap-2 flex">
                <motion.button disabled={rowData.length > 0 || pdfReady} type="submit" className="w-full text-white bg-emerald-700 rounded-xl px-2 py-1.5 font-semibold cursor-pointer flex justify-center items-center gap-0.5 hover:shadow disabled:opacity-40 disabled:cursor-not-allowed">สร้างฟอร์ม</motion.button>
                <motion.button disabled={!pdfReady} onClick={() => setOpenModelDelete(true)} className="w-full text-sm text-center text-rose-700 border-rose-700 !disabled:dark:hover:border-rose-800 border font-semibold px-6 py-2 hover:bg-red-50 !disabled:dark:hover:bg-rose-800 rounded-xl dark:bg-rose-700 dark:text-white cursor-pointer disabled:opacity-40 disabled:cursor-not-allowed" >ลบข้อมูล</motion.button>
              </div>
            </div>
          </motion.form>
        </div>
      </div>

      {!pdfReady &&
        <div className="shadow-lg rounded-2xl">
          <AppDataGrid
            columnDefs={columnDefs}
            rowData={rowData}
            loading={loadingDataTable}
            pagination={false}
            onCellValueChanged={hdlCellValueChanged}
            onGridReady={onGridReady}
          />
        </div>
      }

      {rowData.length > 0 && !pdfReady &&
        <>
          <hr className="mt-8 mb-6" />
          <p className="underline underline-offset-2 select-none">ประเมินคุณภาพการบันทึกเวชระเบียนในภาพรวม</p>
          <div className="flex items-start gap-2 py-2 select-none">
            <div>
              <p className="font-semibold w-[130px] text-sm">Overall Finding</p>
              <p className="font-semibold w-[130px] text-sm">(เลือกเพียง 1 ข้อ)</p>
            </div>
            <div className="flex flex-col gap-4">
              <RadioGroup onValueChange={(value) => hdlChangeReviewStatus(value)} >
                {reviewStatus?.length > 0 && reviewStatus?.map((e) => (
                  <Radio size="sm" value={e.review_status_id} key={e.review_status_id}>{e.review_status_name} ({e.review_status_description})</Radio>
                ))}
              </RadioGroup>
              {reviewStatus && reviewStatus?.find(e => e.review_status_id === formData.review_status_id)?.review_status_type === true && (
                <Input onValueChange={(value) => setFormData(prev => ({ ...prev, review_status_comment: value }))} variant="bordered" labelPlacement="outside" radius="md" label="อื่นๆ" placeholder="อื่นๆ" value={formData.review_status_comment } />
              )}
            </div>
          </div>
        </>
      }

      {pdfReady && ( pdfBase64 === "" ? 
        <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="z-50 fixed inset-0 w-full h-dvh flex items-center justify-center backdrop-blur-xs">
          <LoadingCenter title="กําลังสร้าง PDF..." />
        </motion.div> 
        : 
        <div className="w-full h-[calc(100vh-15vh)] overflow-hidden rounded-2xl my-2 shadow-lg">
          <iframe title="pdf" loading="lazy" width="100%" height="100%" src={`data:application/pdf;base64,${pdfBase64}`} className="w-full h-full" />
        </div>
      )}

      { !pdfReady && (
        <motion.button
          onClick={onSubmit}
          className="w-full mt-4 text-emerald-700 font-semibold py-2 bg-emerald-50 border border-emerald-700 dark:hover:bg-emerald-800 rounded-2xl dark:bg-emerald-700 dark:text-white cursor-pointer disabled:opacity-40 disabled:cursor-not-allowed"
          onMouseDown={(e) => ripple.create(e)}
          disabled={formData.review_status_id === null || formData.review_status_id === undefined || generatePdf || rowData.length === 0 || loadingSubmit || patientVn === null || patientVn === undefined}
        >{generatePdf ? "กำลังบันทึกข้อมูล" : "บันทึก"}</motion.button>
      )}
    </>
  );
}