"use client";

import { Input } from "@heroui/input";
import { TimeInput } from "@heroui/date-input";
import { useEffect, useState } from "react";
import { I18nProvider } from "@react-aria/i18n";
import { DatePicker } from "@heroui/date-picker";
import { parseDate, parseTime } from "@internationalized/date";

const fmtTime = new Intl.DateTimeFormat("th-TH", {
  hour: "2-digit",
  minute: "2-digit",
  second: "2-digit",
  hour12: false,
  timeZone: "Asia/Bangkok",
});

function sanitizeForm(prevForm, dataShape) {
  const next = {};
  for (const key of Object.keys(dataShape)) next[key] = prevForm?.[key] ?? "";
  return next;
}

export default function ClinicDynamicForm({ data, value, onChange, length }) {
  const [form, setForm] = useState(data);

  useEffect(() => {
    setForm(prev => sanitizeForm(prev, data));
  }, [data]);

  const handleChange = (key, val) => {
    setForm(prev => {
      const next = { ...prev, [key]: val };
      const payload = Object.fromEntries(Object.keys(data).map(k => [k, next[k] ?? ""]));
      onChange?.(payload);
      return next;
    });
  };

  return (
    <>
      {Object.entries(data).map(([key]) => {
        if (key === "visit_date") {
          return (
            <I18nProvider locale="th-TH" key={key}>
              <DatePicker
                showMonthAndYearPickers
                label="วันที่เข้ารับบริการ"
                variant="bordered"
                size="md"
                isDisabled={value?.[key] !== undefined || length > 0}
                // ต้องเป็น CalendarDate ไม่ใช่ string
                defaultValue={value?.[key] ? parseDate(String(value[key]).slice(0, 10)) : undefined}
                labelPlacement="outside"
                errorMessage="กรุณาเลือกวันที่"
                onChange={(val) => handleChange(key, val?.toString() ?? "")}
                isRequired
              />
            </I18nProvider>
          );
        }

        if (key === "review_period_start" || key === "review_period_end") {
          const label = key === "review_period_start" ? "เวลาเริ่ม" : "เวลาสิ้นสุด";

          // ทำเป็น controlled ด้วย TimeValue ที่ถูกชนิด
          const timeValue = value?.[key] ? parseTime(String(value[key])) : undefined;

          return (
            <TimeInput
              key={key}
              label={label}
              variant="bordered"
              size="md"
              labelPlacement="outside"
              hourCycle="24"
              granularity="second"
              // ห้ามใส่ defaultValue เป็น string
              isDisabled={timeValue !== undefined || length > 0}
              value={timeValue}
              errorMessage="กรุณากรอกช่วงเวลา"
              onChange={(val) => {
                if (!val) return handleChange(key, "");
                // format เก็บเป็น HH:mm:ss เช่นเดิม
                const d = new Date();
                d.setHours(val.hour ?? 0, val.minute ?? 0, val.second ?? 0, 0);
                handleChange(
                  key,
                  fmtTime.format(d) // HH:mm:ss
                );
              }}
              isRequired
            />
          );
        }

        return (
          <Input
            key={key}
            label="Diag"
            variant="bordered"
            size="md"
            defaultValue={value?.[key] ?? ""}
            isDisabled={value?.[key] !== undefined || length > 0}
            labelPlacement="outside-top"
            onChange={(e) => handleChange(key, (e.target.value).toUpperCase())}
            isRequired
            errorMessage="กรุณากรอกข้อมูล Diag"
          />
        );
      })}
    </>
  );
}
