// components/Developing.jsx
"use client";

import Link from "next/link";
import { motion } from "framer-motion";
import { CodeXml } from "lucide-react";
import Ripple from "@nuttawoot_donut/react-ripple";

/**
 * ส่วนสําหรับแสดงข้อความว่าฟีเจอร์นี้ยังไม่พร้อมให้ใช้งาน
 * Props:
 * - title: หัวข้อหลัก (default: "กำลังพัฒนา")
 * - message: ข้อความหลัก (default: "ฟีเจอร์นี้ยังไม่พร้อมให้ใช้งานตอนนี้")
 * - subMessage: ข้อความรอง (default: "ทีมงานกำลังเร่งทำให้เสร็จแบบโดยเร็วที่สุด")
 * - progress: ความคืบหน้าเป็นเปอร์เซ็นต์ (0-100) (optional)
 * - est: ข้อความประมาณเวลาที่คาดว่าจะเสร็จ (optional)
 * - actions: อาร์เรย์ของปุ่มหรือการกระทำที่ต้องการแสดง (optional)
 * - className: คลาสเพิ่มเติมสำหรับการปรับแต่งสไตล์ (optional)
 * 
 * ตัวอย่างการใช้งาน:
 * - <Developing
 *   - title="ฟีเจอร์ใหม่"
 *   - message="เรากำลังพัฒนาฟีเจอร์นี้อยู่"
 *   - subMessage="คาดว่าจะเสร็จในอีกไม่กี่สัปดาห์"
 *   - progress={45}
 *   - est="คาดว่าจะเสร็จในอีก 2 สัปดาห์"
 *   - actions={[
 *      - { label: "กลับไปหน้าแรก", href: "/", primary: true },
 *      - { label: "ติดต่อเรา", onClick: () => alert("ติดต่อเรา") }
 *   - ]}
 *   - className="my-custom-class"
 * - />
 */

export default function Developing({
    title = "กำลังพัฒนา",
    message = "ฟีเจอร์นี้ยังไม่พร้อมให้ใช้งานตอนนี้",
    subMessage = "ทีมงานกำลังเร่งทำให้เสร็จแบบโดยเร็วที่สุด", 
    progress, // progress เป็นเปอร์เซ็นต์ (0-100)
    est, // estimated time
    actions = [], // เพิ่ม prop actions
    className = "" // เพิ่ม prop className
}) {

  // ฟังก์ชันช่วยสำหรับจัดการ class names
  const clx = (...s) => s.filter(Boolean).join(" ");
  const ripple = new Ripple();

  return (
    <motion.section
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
      aria-live="polite"
      className={clx(
        "w-full rounded-2xl border border-neutral-200 dark:border-neutral-800 bg-white/70 dark:bg-neutral-900/70",
        "backdrop-blur p-4 shadow-sm",
        className
      )}
    >
      {/* Header */}
      <div className="flex items-start gap-4">
        <div
          className="h-12 w-12 shrink-0 rounded-xl border border-neutral-200 dark:border-neutral-800 
                     bg-gradient-to-br from-emerald-100 to-emerald-200 dark:from-emerald-900/40 dark:to-emerald-800/30
                     flex items-center justify-center"
          aria-hidden
        >
          {/* simple wrench icon */}
          <CodeXml className="size-6 text-emerald-700 dark:text-emerald-400" />
        </div>

        <div className="min-w-0">
          <h2 className="text-lg sm:text-xl font-semibold tracking-tight text-neutral-900 dark:text-neutral-50">
            {title}
          </h2>
          <p className="mt-1 text-base font-medium text-neutral-700 dark:text-neutral-300">{message}</p>
          <p className="text-xs text-neutral-500 dark:text-neutral-400">{subMessage}</p>
        </div>
      </div>

      {/* Progress */}
      {(typeof progress === "number" || est) && (
        <div className="mt-6 space-y-2">
          {typeof progress === "number" && (
            <>
              <div className="flex items-center justify-between text-xs text-neutral-500 dark:text-neutral-400">
                <span>ความคืบหน้า</span>
                <span aria-live="polite">
                  <span className="font-medium">{Math.max(0, Math.min(100, progress))}</span>%
                </span>
              </div>
              <div className="h-2 w-full rounded-full bg-neutral-200 dark:bg-neutral-800 overflow-hidden">
                <div
                  className="h-full rounded-full bg-emerald-600 dark:bg-emerald-400 transition-[width] duration-500"
                  style={{ width: `${Math.max(0, Math.min(100, progress))}%` }}
                  role="progressbar"
                  aria-valuemin={0}
                  aria-valuemax={100}
                  aria-valuenow={Math.max(0, Math.min(100, progress))}
                />
              </div>
            </>
          )}
          {est && <p className="text-xs text-neutral-500 dark:text-neutral-400">{est}</p>}
        </div>
      )}

      {/* Actions */}
      {actions.length > 0 && (
        <div className="mt-6 flex flex-wrap gap-2">
          {actions.map((a, i) => {
            const base =
              "inline-flex items-center gap-2 rounded-lg px-3 py-2 text-sm transition border focus:outline-none focus:ring-2 focus:ring-emerald-400/60";
            const primary =
              "bg-emerald-600 text-white border-emerald-600 hover:bg-emerald-700";
            const secondary =
              "bg-transparent text-neutral-700 dark:text-neutral-200 border-neutral-300 dark:border-neutral-700 hover:bg-neutral-50 dark:hover:bg-neutral-800/60 cursor-pointer";
            const cls = clx(base, a.primary ? primary : secondary);
            const content = <span className="whitespace-nowrap">{a.label}</span>;

            if (a.href) {
              return (
                <Link draggable={false} key={i} href={a.href} className={cls} onMouseDown={(e) => ripple.create(e, "dark")} rel="noreferrer">
                  {content}
                </Link>
              );
            }
            return (
              <button key={i} type="button" onClick={a.onClick} onMouseDown={(e) => ripple.create(e, "light")} className={cls}>
                {content}
              </button>
            );
          })}
        </div>
      )}
    </motion.section>
  );
}
